
import { defineComponent, reactive } from 'vue'
import { POSInfo, ICM21700111_REQ_BODY, ICM21700111_RES } from "@/shared/type/ICM215001SC00.type";
import { PAGE_SIZE } from "@/shared/constants/common.const";
import Pagination from '@/shared/components/Pagination/Pagination.vue'
import BreadCrumb from '@/shared/components/BreadCrumb/BreadCrumb.vue'
import SelectOption from "@/shared/components/SelectOption/SelectOption.vue";

export default defineComponent({
    name: "icm215001sc00",
    components: {
        Pagination,
        BreadCrumb,
        SelectOption
    },
    data() {
        const dataTable: POSInfo[] = []
        const tempTable = [] as any
        const input = reactive({
            displayYN: "",
            pOSMerchantTypeCode: "",
            pOSMerchantCode: "",
            discountRate: null,
            pOSMerchantPhoneNo: "",
            staffPhoneNo: "",
            pOSMerchantAddress: "",
            pOSMerchantName: ""
        });
        return {
            routePOS: "pos-merchant/pos-merchant-edit",
            headers: [
                { columnName: this.$t('icm215001sc00.no') },
                { columnName: this.$t('icm215001sc00.view') },
                { columnName: this.$t('icm215001sc00.display') },
                { columnName: this.$t('icm215001sc00.pos_merchant_code') },
                { columnName: this.$t('icm215001sc00.pos_merchant_name') },
                { columnName: this.$t('icm215001sc00.pos_merchant_address') },
                { columnName: this.$t('icm215001sc00.status') },
                { columnName: this.$t('icm215001sc00.discount_rate') },
                { columnName: this.$t('icm215001sc00.pos_merchant_phone_no') },
                { columnName: this.$t('icm215001sc00.staff_phone_no') },
                { columnName: this.$t('icm215001sc00.pos_merchant_biz_type') }
            ],
            dataTable,
            tempTable,
            input,
            searchValue: '',
            searchKeyword: '',
            statusCode: '',
            totalItems: 0,
            currentPage: 1,
            pageSize: PAGE_SIZE,
            startingIndex: 1,
            isReset: false,
            breadcrumbs: [
                { value: 'apm201001sc00.label.home', keyPath: '/COM200001SC00', type: 'route' },
                { value: 'icm215001sc00.contents_Policy_management', keyPath: '', type: 'label' },
                { value: 'icm215001sc00.pos_merchant', keyPath: '', type: 'label' },
                { value: 'icm215001sc00.pos_merchant_list', keyPath: '', type: 'label' }
            ]
        }
    },
    mounted() {
        this.getPOSMerchantInfoList();
    },
      computed: {
        YnType(): any {
            return this.$store.state.CommonCodeModule.commonCode.YnType
        },
        POSMerchantTypeCode(): any {
            return this.$store.state.CommonCodeModule.commonCode.POSMerchantTypeCode
        },
        ContentManangementStatusCode(): any {
            return this.$store.state.CommonCodeModule.commonCode.ContentManangementStatusCode
        }
    },
    methods: {
    // Mapping Status Code
    onMappingTableData(lists) {
      const { POSMerchantTypeCode , ContentManangementStatusCode, YnType } = this.$store.state.CommonCodeModule.commonCode;
      return lists.map((item) => ({
            pOSMerchantCode: item.pOSMerchantCode,
            statusCode : ContentManangementStatusCode && item.statusCode ? `${ ContentManangementStatusCode.find((type) => type.key === item.statusCode) ?.key}: ${ ContentManangementStatusCode.find((type) => type.key === item.statusCode )?.value }` : "",
            pOSMerchantTypeCode: POSMerchantTypeCode && item.pOSMerchantTypeCode ? `${ POSMerchantTypeCode.find((type) => type.key === item.pOSMerchantTypeCode) ?.key}: ${ POSMerchantTypeCode.find((type) => type.key === item.pOSMerchantTypeCode )?.value }` : "",
            displayYN: YnType && item.displayYN ? this.$codeConverter.convertValue('YnType', item.displayYN) : "",
            pOSMerchantName: item.pOSMerchantName,
            staffPhoneNo: item.staffPhoneNo,
            pOSMerchantAddress: item.pOSMerchantAddress,
            discountRate: item.discountRate,
            pOSMerchantPhoneNo: item.pOSMerchantPhoneNo 
      }));
    },
        handleClickRow(index, rowData) {
            this.$router.push({
                path: '/ICM215001SC01',
                query: {
                    type: 'edit',
                    pOSMerchantCode: rowData.pOSMerchantCode
                }
            })
        },
        handleClickRegister() {
            this.$router.push({
                path: '/ICM215001SC01',
                query: {
                    type: 'register'
                }
            })
        },

        // Display Search 
        handleSearchDisplay() {
            if (this.input.displayYN == 'All') {
                return this.dataTable
            } else if (this.input.displayYN !== 'All') {
                this.dataTable = this.dataTable.filter(
                    (search: any) => {
                        return search.displayYN == this.input.displayYN;
                    });
            }
        },
        // Get Data list ICM21700111
        async getPOSMerchantInfoList(mode?: string) {
            const body: ICM21700111_REQ_BODY = {
                pageSize: this.pageSize,
                pageNumber: mode == 'search' ? 0 : this.currentPage,
                displayYN: this.input.displayYN,
                statusCode: this.statusCode,
                searchValue: this.searchKeyword.trim()
            };
            if (mode == 'search') {
                this.isReset = true;
                this.currentPage = 1
            }
            const responseData: any = await this.$request.getRequest("ICM21700111", body, false) as ICM21700111_RES
            if (responseData.header.result) {
                this.dataTable = responseData.body.list
                this.dataTable = this.onMappingTableData(responseData.body.list);
                this.totalItems = responseData.body.totalCount;
            }
        },
        // Clear Search 
        async refreshSearch() {
            this.isReset = true;
            this.currentPage = 1;
            this.searchKeyword = "";
            this.searchValue = "";
            this.input.displayYN = "";
            this.statusCode = ''
            await this.handleSearchDisplay();
            await this.getPOSMerchantInfoList();
        },
        // Function Pagination
        handlePagination(pageNumber: number) {
            this.isReset = false;
            this.currentPage = pageNumber
            this.getPOSMerchantInfoList()
        },
        handleStartingIndex(startingIndex: number) {
            this.startingIndex = startingIndex
        }
    }
})
